import { h, JSX } from '@stencil/core';
import { isModelFuelTypeGasoline, isModelName, isString } from '../../utility/helper';

export default function SideDrawerItemText({
  isActive,
  text,
  subtext,
  renderSignatures
}: {
  isActive: boolean;
  text: string;
  subtext: string;
  renderSignatures: boolean;
}): JSX.Element {
  return (
    <div class="text-container">
      {renderSignatures && isModelName(text) ? (
        <phn-p-model-signature model={text.toLowerCase()} size="inherit" class="model-signature" />
      ) : (
        <phn-p-text size="inherit" weight={isActive ? 'semibold' : 'regular'} color="inherit" tag="div">
          {text}
        </phn-p-text>
      )}

      {isString(subtext) && !isModelFuelTypeGasoline(text) ? (
        <div class="text-limit">
          <phn-p-text
            color="inherit"
            class="subtext"
            weight="regular"
            size="x-small"
            tag="div"
            ellipsis="true"
            data-test-id="subtext"
          >
            {subtext}
          </phn-p-text>
        </div>
      ) : null}
    </div>
  );
}
